import { bookService } from "@/services/book.service";

export default {
    state: {
        books: null,
        booksFilterBy: null,
    },
    getters: {
        booksToShow(state) {
            const { books } = state;
            if (!state.booksFilterBy) return books;
            return books?.filter((book) => {
                return (
                    book.title.toLowerCase().includes(state.booksFilterBy.txt) &&
                    book.listPrice.amount > state.booksFilterBy.minPrice &&
                    book.listPrice.amount < state.booksFilterBy.maxPrice
                );
            });
        }
    },
    mutations: {
        setBooks(state, { books }) {
            state.books = books;
        },
        setBooksFilterBy(state, { filterBy }) {
            state.booksFilterBy = filterBy;
        },
        saveBook(state, { book }) {
            const bookIdx = state.books.findIndex(currBook => book.id === currBook.id);
            if (bookIdx === -1) state.books.push(book);
            else state.books.splice(bookIdx, 1, book);
        },
        
    },
    actions: {
        async loadBooks({ commit }) {
            const books = await bookService.query();
            commit({ type: 'setBooks', books })
        },
        async saveBook({ commit }, { book }) {
            const savedBook = await bookService.save(book);
            commit({ type: 'saveBook', book: savedBook });
            return savedBook;
        },
        // async removeBook({ commit }, { bookId }) {
        //     await bookService.remove(bookId);
        //     commit({ type: 'removeBook', book: bookId });
        // }
    },
    modules: {}
}