<template>
  <section class="book-filter flex align-center justify-center">
    <label for="search">Search a book: </label>
    <input
      id="search"
      type="text"
      placeholder="Search"
      v-model="filterBy.txt"
      @input="emitFilter"
    />
    <label for="min">From:</label>
    <input
      class="filter-price"
      id="min"
      type="number"
      v-model="filterBy.minPrice"
      @input="emitFilter"
    />
    <label for="max">To:</label>
    <input
      class="filter-price"
      id="max"
      type="number"
      v-model="filterBy.maxPrice"
      @input="emitFilter"
    />
  </section>
</template>

<script>
export default {
  data() {
    return {
      filterBy: {
        txt: "",
        minPrice: 0,
        maxPrice: Infinity,
      },
    };
  },
  methods: {
    emitFilter() {
      this.$emit("filtered", { ...this.filterBy });
    },
  },
};
</script>

<style lang="scss">
.book-filter {
  .filter-price {
    width: 50px;
  }
}
@media (max-width: 740px) {
  .book-filter {
    flex-direction: column;
    .filter-price {
      width: 40px;
    }
  }
}
</style>