<template>
  <section class="long-txt">
    <p>{{ getTxt }}</p>
    <button @click="toggleButton" :class="lessThenLimit">
      {{ readLessMoreButton }}
    </button>
  </section>
</template>

<script>
export default {
  props: ["txt"],
  data() {
    return {
      isShowAll: false,
    };
  },
  computed: {
    getTxt() {
      const limit = 100;
      var txt = "";
      if (this.txt.length <= limit || this.isShowAll) txt = this.txt;
      else txt = this.txt.substring(0, limit) + "...";
      return txt;
    },

    readLessMoreButton() {
      if (this.isShowAll) return "Read less";
      else return "Read more";
    },

    lessThenLimit() {
      if (this.txt.length <= 100) return "hide";
    },
  },
  methods: {
    toggleButton() {
      this.isShowAll = !this.isShowAll;
    },
  },
};
</script>

<style lang="scss">
.long-txt {
  button {
    background: transparent;
  }
}
</style>
