<template>
  <label class="switch">
    <input type="checkbox" :checked="value" @change="toggleBtn" />
    <div></div>
  </label>
</template>
<script>
export default {
  props: ['value'],
  data() {
    return {
      isOn: this.value,
    };
  },
  methods: {
    toggleBtn() {
      this.isOn = !this.isOn;
      this.$emit('input', this.isOn);
    },
  },
};
</script>