<template>
  <form class="review-add" v-if="review" @submit.prevent="emitReview">
    <input ref="fullName" type="text" placeholder="Full name" v-model="review.fullName" />
    <input type="date" v-model="review.datepicker" />
    <vue-stars v-model="review.rating" />
    <textarea v-model="review.txt" />
    <button>Add</button>
  </form>
</template>

<script>
import { bookService } from "@/services/book.service";
import { VueStars } from "vue-stars";
export default {
  data() {
    return {
      review: null,
    };
  },
  created() {
    this.review = bookService.getEmptyReview();
  },
  computed: {},
  methods: {
    emitReview() {
      this.$emit("reviewed", this.review);
      this.review = bookService.getEmptyReview();
    },
  },
  components: {
    VueStars,
  },
   mounted() {
    this.$refs.fullName.focus();
  },
};
</script>

