<template>
  <label class="gender">
    <select @change="toggleBtn">
      <!-- <option disabled value="">Please select one</option> -->
      <option>Male</option>
      <option>Female</option>
      <option>i'd rather not say</option>
    </select>
  </label>
</template>
<script>
export default {
  props: ["value"],
  data() {
    return {
      gender: "",
    };
  },
  methods: {
    toggleBtn() {
      this.isOn = !this.isOn;
      this.$emit("input", this.gender);
    },
  },
};
</script>