<template>
  <li class="book-preview flex column">
    <router-link :to="`/book/${book.id}`">
      <div class="book-img">
        <img :src="book.thumbnail" />
        <img class="sale-img" :src="saleImg" />
      </div>
    </router-link>
    <div class="book-info">
      <h3 class="title">{{ book.title }}</h3>
      <h3 class="price">Price: {{ getCurrency }}{{ book.listPrice.amount }}</h3>
    </div>
  </li>
</template>

<script>
export default {
  props: ["book"],
  computed: {
    getCurrency() {
      var mapCurrency = {
        EUR: "",
        ILS: "",
        USD: "$",
      };
      return mapCurrency[this.book.listPrice.currencyCode];
    },
    saleImg() {
      if(this.book.listPrice.isOnSale) return require(`@/assets/img/sale.png`);
    },
  },
};
</script>