<template>
  <li class="review-preview">
    <button title="delete" @click="remove">X</button>
    <div class="flex column align-center">
      <h2>from: {{ review.fullName }}</h2>
      <h2>{{ ratingStar }}</h2>
      <h2>{{ review.datepicker }}</h2>
      <p>{{ review.txt }}</p>
    </div>
  </li>
</template>

<script>
export default {
  props: ["review"],
  created() {},
  computed: {
    ratingStar() {
      const stars = "".repeat(this.review.rating);
      return stars;
    },
  },
  methods: {
    remove() {
      this.$emit("removeReview", this.review.id);
    },
  },
};
</script>

<style lang="scss">
.review-preview {
  box-shadow: 0px 1px 5px 0 rgba(60, 64, 67, 0.302),
    0 1px 3px 1px rgba(60, 64, 67, 0.149);
  button {
    background: transparent;
  }
  h2 {
    font-size: 1rem;
  }
}
</style>


