
<template>
  <section class="user-msg" :class="classSuccess" v-if="msg">
    <button @click="msg = null">x</button>
    <p>{{ msg.txt }}</p>
    <div v-if="msg.link">
      <router-link :to="msg.link">Check it out</router-link>
    </div>
  </section>
</template>

<script>
import eventBus from "@/services/eventBus.service";

export default {
  data() {
    return {
      msg: null,
    };
  },
  created() {
    eventBus.$on("showMsg", (msg) => {
      this.msg = msg;
      setTimeout(() => {
        this.msg = null;
      }, 3000);
    });
  },
  computed: {
    classSuccess() {
      return this.msg?.type === "success" ? "success" : "fail";
    },
  },
  destroyed() {
    eventBus.$off("showMsg");
  },
};
</script>
