<template>
  <main class="book-app">
    <book-filter @filtered="setFilter" />
    <div class="add-book-container flex column align-center">
      <book-add @searchBook="searchBook" />
      <modal-options
        v-if="options.length"
        @addBook="addBook"
        :options="options"
      />
    </div>
    <book-list :books="booksForDisplay" />
  </main>
</template>

<script>
import { bookService } from "@/services/book.service";
// @ is an alias to /src
import BookList from "@/cmps/BookList.vue";
import BookFilter from "@/cmps/BookFilter.vue";
import BookAdd from "@/cmps/BookAdd.vue";
import ModalOptions from "@/cmps/ModalOptions.vue";
import eventBus from "@/services/eventBus.service";

export default {
  data() {
    return {
      options: [],
    };
  },
  async created() {
    this.loadBooks();
  },
  methods: {
    async loadBooks() {
      this.$store.dispatch({ type: "loadBooks" });
    },
    async searchBook(name) {
      if (!name) {
        this.options = [];
        return;
      }
      const res = await bookService.getBoooksFromApi(name);
      this.options = res.items;
    },
    async addBook(book) {  
      const newBook = await this.$store.dispatch({ type: "saveBook", book });
      const msg = { txt: `Book ${newBook.title} saved`, link: `/book/${newBook.id}`, type: "success" };
      eventBus.$emit("showMsg", msg);
      this.options = [];
    },
    setFilter(filterBy) {
      this.$store.commit({ type: "setBooksFilterBy", filterBy });
    },
  },

  computed: {
    booksForDisplay() {
      return this.$store.getters.booksToShow;
    },
  },

  components: {
    BookList,
    BookFilter,
    BookAdd,
    ModalOptions,
  },
};
</script>


<style lang="scss">
.book-app {
  padding-top: 120px;
}
</style>
