<template>
  <section>
    <div class="book-details container" v-if="book">
      <div class="prev-next-links flex space-between">
        <router-link :to="`/book/${nextBookId}`">Next</router-link>
        <router-link :to="`/book/${prevBookId}`">Previous</router-link>
      </div>
      <div class="book flex space-around align-center">
        <div class="book-info">
          <h2>{{ book.title }}</h2>
          <h2 :class="classNameByPrice">
            {{ getCurrency }}{{ book.listPrice.amount }}
          </h2>
          <p>{{ book.subtitle }}</p>
          <p>{{ getPageCount }}</p>
          <p>{{ getPublishedDate }}</p>
          <long-txt v-bind:txt="book.description" />
        </div>
        <img class="book-img" :src="book.thumbnail" />
      </div>
      <div class="review">
        <ul class="review-list review-grid clean-list" v-if="book.reviews">
          <h3 class="review-list-header">Reviews about this book:</h3>
          <review-preview
            v-for="currReview in book.reviews"
            :key="currReview.id"
            :review="currReview"
            @removeReview="removeReview"
          />
        </ul>
        <div v-else>There are no reviews for this book, add your own!</div>
        <button class="add-btn" v-if="isShowReview" @click="onToggleReview">
          Add your review
        </button>

        <div class="animate__animated animate__backInUp" v-else>
          <button class="add-btn" @click="onToggleReview">Close</button>
          <review-add @reviewed="addReview" />
        </div>
      </div>
    </div>
  </section>
</template>



<script>
import { bookService } from "@/services/book.service";
import eventBus from "@/services/eventBus.service";
import LongTxt from "@/cmps/LongTxt.vue";
import ReviewAdd from "@/cmps/ReviewAdd.vue";
import ReviewPreview from "@/cmps/ReviewPreview.vue";

export default {
  data() {
    return {
      book: null,
      isShowReview: true,
      nextBookId: "",
      prevBookId: "",
    };
  },
  computed: {
    getPageCount() {
      if (this.book.pageCount > 500) return "Long reading";
      else if (this.book.pageCount > 200) return "Decent Reading";
      else return "Light Reading";
    },
    getPublishedDate() {
      const year = new Date().getFullYear();
      var classPublish = "";
      if (year - this.book.publishDate > 10) classPublish = "Veteran Book";
      else if (year - this.book.publishDate < 1) classPublish = "New Book!";
      return classPublish;
    },
    classNameByPrice() {
      if (this.book.listPrice.amount > 150) return "red";
      else if (this.book.listPrice.amount < 20) return "green";
      else return "blue";
    },
    getCurrency() {
      var mapCurrency = {
        EUR: "",
        ILS: "",
        USD: "$",
      };
      return mapCurrency[this.book.listPrice.currencyCode];
    },
  },
  async created() {
    this.loadBook();
  },
  methods: {
    async loadBook() {
      const bookId = this.$route.params.bookId;
      const book = await bookService.getById(bookId);
      var bookToUse = JSON.parse(JSON.stringify(book));
      const prevNext = await bookService.getNextPrev(bookId);
      this.nextBookId = prevNext.nextBookId;
      this.prevBookId = prevNext.prevBookId;
      this.book = bookToUse;
    },
    async addReview(review) {
      await bookService.addReview(this.book.id, review);
      this.onToggleReview();
      const msg = { txt: "Review saved", link: "", type: "success" };
      eventBus.$emit("showMsg", msg);
      this.loadBook();
    },
    onToggleReview() {
      this.isShowReview = !this.isShowReview;
    },
    async removeReview(reviewId) {
      await bookService.removeReview(reviewId, this.book.id);
      this.loadBook();
    },
  },
  watch: {
    async $route(from, to) {
      this.loadBook();
    },
  },
  components: {
    LongTxt,
    ReviewAdd,
    ReviewPreview,
  },
};
</script>
