<template>
  <ul class="modal-options clean-list">
    <li v-for="option in options" :key="option.id" @click="add(option)">
      {{ option.volumeInfo.title }}
    </li>
  </ul>
</template>


<script>
export default {
  props: ["options"],
  methods: {
    add(option) {
      this.$emit("addBook", option);
    },
  },
};
</script>
