.modal-options {
  margin: 7px;
  box-shadow: 0px 1px 5px 0 rgba(60, 64, 67, 0.302),
    0 1px 3px 1px rgba(60, 64, 67, 0.149);
  border: none;
  margin-right: 5px;
  border-radius: 5px;
  padding: 7px;
  outline: none;
  margin-top: 5px;
  font-size: 1.2rem;
  width: 300px;
  li {
    border-bottom: 1px solid black;
    cursor: pointer;
  }
