<template>
  <nav class="main-nav main-container full">
    <div class="main-header flex space-between align-center">
      <div class="logo">
        <router-link to="/">Miss Book</router-link>
      </div>
      <div class="links" :class="classMenu">
        <router-link @click.native="toggleMenu" to="/">Home</router-link>
        <router-link @click.native="toggleMenu" to="/book">Books</router-link>
        <!-- <router-link @click.native="toggleMenu" to="/user">Profile</router-link> -->
        <router-link @click.native="toggleMenu" to="/about">About</router-link>
      </div>
      <button @click="toggleMenu" class="menu-btn"><menu-icon /></button>
    </div>
  </nav>
</template>

<script>
import MenuIcon from "vue-material-design-icons/Menu.vue";

export default {
  data() {
    return {
      isMobileMenuOpen: false,
    };
  },
  computed: {
    classMenu() {
      if (this.isMobileMenuOpen) return "menu-open";
    },
  },
  methods: {
    toggleMenu() {
      this.isMobileMenuOpen = !this.isMobileMenuOpen;
    },
  },
  components: {
    MenuIcon,
  },
};
</script>
