<template>
  <form class="book-add">
    <input
      type="text"
      placeholder="Add Book"
      v-model="name"
      @input="emitSearch"
    />
  </form>
</template>

<script>
import { bookService } from "@/services/book.service";

export default {
  data() {
    return {
      name: null,
    };
  },
  methods: {
    emitSearch() {
      this.$emit("searchBook", this.name);
    },
  },
  components: {
    bookService,
  },
};
</script>

<style lang="scss">
.book-add {
  input {
    width: 300px;
  }
}
</style>